* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  color: #393c41;
  margin: 0;
  padding: 0;
  scroll-snap-type: y mandatory;
}

body {
  width: 100%;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

@keyframes animateDown {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(5px);
  }

  60% {
    transform: translateY(3px);
  }
}
